import { Card, CardContent, CardHeader, Link, Typography } from "@mui/material";
import LinkedInIcon from '@mui/icons-material/LinkedIn';
function Footer(){
    return(
        <Card className="footer gradient-card">
            <CardHeader title="Kontakt"/>   
            <CardContent>
                Jeli jeste zainteresowany wspprac lub masz pytania, miao si ze mn skontaktuj. Jeste serdecznie zaproszony do odwiedzenia mojego portfolio, gdzie znajdziesz wicej informacji o mojej pracy i projektach.
            <Typography><Link href="" ><LinkedInIcon/></Link></Typography>
            </CardContent>
        </Card>
    )
}
export default Footer;